@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.form-label {
	height: 30px;
	font-size: 14px;
}
.errorMsg {
	color: red !important;
}

input[type="text"],
input[type="email"] {
	height: 32px;
	font-size: 14px;
	padding: 10px;
}
select {
	width: 73px !important;
	height: 32px !important;
}
.ExpMonthModal {
	width: 100px !important;
	margin-right: 10px;
}
.ExpYearModal {
	width: 80px !important;
	margin-right: 10px;
}

* {
	font-family: "Montserrat", sans-serif;
}
p {
	font-size: 14px;
}
h5 {
	font-size: 16px;
	font-weight: 600;
}

@media (min-width: 576px) {
	.modal-dialog {
		max-width: 840px;
	}
}

